import React, {useEffect, useState} from "react";
import {
  Box, Button,
  createTheme,
  Dialog, DialogActions,
  DialogContent,
  DialogContentText,
  IconButton,
  Stack,
  ThemeProvider, Typography
} from "@mui/material";
import Timer from "./Timer";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import PauseIcon from "@mui/icons-material/Pause";
import AudiotrackIcon from '@mui/icons-material/Audiotrack'
import DraftsIcon from '@mui/icons-material/Drafts';
import ShareIcon from '@mui/icons-material/Share';
import FlagIcon from '@mui/icons-material/Flag';
import {useNavigate} from "react-router-dom";
import axios from "axios";

function Tool(props) {
  const [play, setPlay] = useState(false);    //  false    true  
  const [visible, setVisible] = useState(false);
  const [result, setResult] = useState(true);
  const [result2, setResult2] = useState(false);
  const [result3, setResult3] = useState(true);
  const [first, setFirst] = useState(0);
  const [ws, setWs] = useState(props.ws);
  const navi = useNavigate();
  
  const theme = createTheme({
    palette: {
      primary: {
        main: '#FF5733',
      },
      bluegray: {
        main: '#eceff1',
      },
    },
  });
  
  useEffect(() => {
    // console.log(props.chatt);
    console.log(props.socketData);
    const item = props.socketData;
    if (item !== undefined) {
      if (item.topic === 'apply' && item.name !== props.name) {
        setResult2(true);
      }
      // if (item.topic === 'answer' && item.name !== props.name) {
      //   setResult(false);
      // }
      
    }

    //   
    if ( props.chatt.length > 0 && props.chatt[props.chatt.length-1].topic === 'game' && first == 0) {
      setPlay(true);
      const audio = document.getElementById('myAudio');
      audio.play();
      setFirst(1);
    }
  }, [props.chatt])
  
  
  const handlePlay = () => {
    const audio = document.getElementById('myAudio');
    audio.play();
    setPlay(true);
  };
  
  const handlePause = () => {
    const audio = document.getElementById('myAudio');
    audio.pause();
    setPlay(false);
  };
  
  const handleInvite = () => {
    let url = '';
    const textarea = document.createElement("textarea");
    document.body.appendChild(textarea);
    url = window.document.location.href;
    textarea.value = url;
    textarea.select();
    document.execCommand("copy");
    document.body.removeChild(textarea);
    alert("  .");
  };
  
  const handleShare = () => {
    let url = '';
    const textarea = document.createElement("textarea");
    document.body.appendChild(textarea);
    url = window.document.location.href;
    const pos1 = url.indexOf('playground');
    url = url.substring(0, pos1);
    url = url.concat('main');
    textarea.value = url;
    textarea.select();
    document.execCommand("copy");
    document.body.removeChild(textarea);
    alert('  .');
  };
  
  //      
  const handleSurrender = () => {
    setVisible(true);
  };
  
  //  ? => 
  const surrenYes = () => {
    const data = {
      color: props.color,
      topic: 'surren',
      msg: 'yes',
      date: new Date().toLocaleString(),
    }
    
    const temp = JSON.stringify(data);
  
    if (ws.current.readyState === 0) { //readyState     
      ws.current.onopen = () => { //webSocket   , 
        console.log(ws.current.readyState);
        ws.current.send(temp);
      }
    } else {
      ws.current.send(temp);
    }
    
    setVisible(false);
    setResult(true);
    
  }
  
  //    =>   
  const winnerYes = () => {
    setResult(false);
  }
  
  //     
  const exit = () => {
    const data = {
      name: props.name,
      topic: 'exit',
      msg: 'exit',
      date: new Date().toLocaleString(),
    }
  
    const temp = JSON.stringify(data);
    
    if (ws.current.readyState === 0) {
      ws.current.onopen = () => {
        console.log(ws.current.readyState);
        ws.current.send(temp);
      }
    } else {
      ws.current.send(temp);
    }
    
    navi('/');

  }
  
  //    
  const reGame = () => {
    axios.get(`http://3.39.195.198:8080/server/getIp`) // ip  axios
      .then(res => {
        console.log(res.data);
        const data = {
          name: props.name,
          topic: 'apply',
          msg: 'regame',
          ip: res.data,
          date: new Date().toLocaleString(),
        };  // (JSON)
  
        const temp = JSON.stringify(data);
        if (ws.current.readyState === 0) {
          ws.current.onopen = () => {
            console.log(ws.current.readyState);
            ws.current.send(temp);
          }
        } else {
          ws.current.send(temp);
        }
        setResult(false);
      })
      .catch(err => {
        alert(` `);
      });
  }
  
  //  
  const accept = () => {
    const data = {
      name: props.name,
      topic: 'answer',
      msg: 'yes',
      ip: props.socketData.ip,
      date: new Date().toLocaleString(),
    };  // (JSON)
    
    const temp = JSON.stringify(data);
    
    if (ws.current.readyState === 0) { //readyState     
      ws.current.onopen = () => { //webSocket   , 
        console.log(ws.current.readyState);
        ws.current.send(temp);
      }
    } else {
      ws.current.send(temp);
    }
    navi(`/playground/${props.socketData.ip}`);
    window.location.reload();
  }
  
  //  
  const reject = () => {
    const data = {
      name: props.name,
      topic: 'answer',
      msg: 'no',
      date: new Date().toLocaleString(),
    }
    const temp = JSON.stringify(data);
  
    if (ws.current.readyState === 0) {
      ws.current.onopen = () => {
        console.log(ws.current.readyState);
        ws.current.send(temp);
      }
    } else {
      ws.current.send(temp);
    }
    navi('/');
  }
  
  return (
    <div>
      <Stack spacing={2} direction={'row'} sx={{marginY: 2, display: 'flex', justifyContent: 'space-between', width: '80%'}}>
        <Timer theme={theme} ws={props.ws} chatt={props.chatt} socketData={props.socketData} color={props.color}></Timer>
        <Box>
          <ThemeProvider theme={theme}>
            <AudiotrackIcon sx={{alignSelf: 'center'}} color={'bluegray'}></AudiotrackIcon>
            {play ?
              <IconButton sx={{opacity: '50%'}} color={'bluegray'} onClick={handlePause}><PauseIcon/></IconButton> :
              <IconButton sx={{opacity: '50%'}} color={'bluegray'} onClick={handlePlay}><PlayArrowIcon/></IconButton>}
            <audio src="/audio/Hawaii%20Five%20O_Theme%20Song.mp3" id={'myAudio'} loop hidden></audio>
          </ThemeProvider>
        </Box>
        <Box>
          <ThemeProvider theme={theme}>
            <IconButton color={'bluegray'} onClick={handleInvite}><DraftsIcon/></IconButton>
            <IconButton color={'bluegray'} onClick={handleShare}><ShareIcon/></IconButton>
            <IconButton color={'bluegray'} onClick={handleSurrender}><FlagIcon/></IconButton>
          </ThemeProvider>
        </Box>
      </Stack>
  
      {/*  */}
      
      <Dialog open={visible} onClose={() => setVisible(false)}>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <Stack spacing={5} direction={'row'} sx={{
              justifyContent: 'center',
              alignItems: 'center',
              paddingY: 2,
            }}>
              {/*<img src="/img/main/blackStone.png" alt="" className={'w-25 h-25'}/>*/}
              <Typography variant="h3" color={'black'}><h1><b> ?</b></h1></Typography>
            </Stack>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button className={'game-re'} variant="contained" color="success"
          onClick={surrenYes}>
            
          </Button>
          <Button className={'game-exit'} variant="contained" color="error"
                  onClick={() => setVisible(false)}></Button>
        </DialogActions>
      </Dialog>

    {/*    */}
      {
        props.chatt
        .filter(item => item.topic === 'finish')
        .map((item, idx) => (
          item.color === props.color ?
            <div key={idx}>
              <Dialog open={result} onClose={() => setResult(false)}>
                <DialogContent>
                  <DialogContentText id="alert-dialog-description">
                    <Stack spacing={5} direction={'row'} sx={{
                      justifyContent: 'center',
                      alignItems: 'center',
                      paddingY: 2,
                    }}>
                      <img src={props.color === 'black' ? "/img/main/blackStone.png" : "/img/main/whiteStone.png"} alt="" className={'w-25 h-25'}/>
                      <Typography variant="h3" color={'blue'}><h1><b>YOU WIN !!</b></h1></Typography>
                    </Stack>
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button className={'game-re'} variant="contained" color="success"
                          onClick={winnerYes}></Button>
                  <Button className={'game-exit'} variant="contained" color="error"
                          onClick={exit}></Button>
                </DialogActions>
              </Dialog>
            </div>
            :
            <div key={idx}>
              <Dialog open={result} onClose={() => setResult(false)}>
                <DialogContent>
                  <DialogContentText id="alert-dialog-description">
                    <Stack spacing={5} direction={'row'} sx={{
                      justifyContent: 'center',
                      alignItems: 'center',
                      paddingY: 2,
                    }}>
                      <img src={props.color === 'black' ? "/img/main/blackStone.png" : "/img/main/whiteStone.png"} alt="" className={'w-25 h-25'}/>
                      <Typography variant="h3" color={'red'}><h1><b>YOU LOSE</b></h1></Typography>
                    </Stack>
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button className={'game-re'} variant="contained" color="success"
                          onClick={reGame}> </Button>
                  <Button className={'game-exit'} variant="contained" color="error"
                          onClick={exit}></Button>
                </DialogActions>
              </Dialog>
            </div>
        ))
      }
    {/*    */}
      {
        props.chatt
          .filter(item => item.topic === 'surren')
          .map((item, idx) => (
            item.color === props.color ?
              //  
              <div key={idx}>
                <Dialog open={result} onClose={() => setResult(false)}>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                      <Stack spacing={5} direction={'row'} sx={{
                        justifyContent: 'center',
                        alignItems: 'center',
                        paddingY: 2,
                      }}>
                        <img src={props.color === 'black' ? "/img/main/blackStone.png" : "/img/main/whiteStone.png"} alt="" className={'w-25 h-25'}/>
                        <Typography variant="h3" color={'red'}><h1><b>YOU LOSE</b></h1></Typography>
                      </Stack>
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button className={'game-re'} variant="contained" color="success"
                            onClick={reGame}> </Button>
                    <Button className={'game-exit'} variant="contained" color="error"
                            onClick={exit}></Button>
                  </DialogActions>
                </Dialog>
              </div>
              :
              //  
              <div key={idx}>
                <Dialog open={result} onClose={() => setResult(false)}>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                      <Stack spacing={5} direction={'row'} sx={{
                        justifyContent: 'center',
                        alignItems: 'center',
                        paddingY: 2,
                      }}>
                        <img src={props.color === 'black' ? "/img/main/blackStone.png" : "/img/main/whiteStone.png"} alt="" className={'w-25 h-25'}/>
                        <Typography variant="h3" color={'blue'}><h1><b>YOU WIN !!</b></h1></Typography>
                      </Stack>
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button className={'game-re'} variant="contained" color="success"
                    onClick={winnerYes}></Button>
                    <Button className={'game-exit'} variant="contained" color="error"
                            onClick={exit}></Button>
                  </DialogActions>
                </Dialog>
              </div>
          ))
      }
    {/*        */}
      {
        props.chatt
          .filter(item => item.topic === 'exit')
          .map((item, idx) => (
            item.msg === 'exit' && item.name != props.name ?
              <div key={idx}>
                <Dialog open={result} onClose={() => setResult(false)}>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                      <Stack spacing={5} direction={'row'} sx={{
                        justifyContent: 'center',
                        alignItems: 'center',
                        paddingY: 2,
                      }}>
                        <Typography variant="h3" color={'black'}><h1><span className={'text-primary'}>{item.name}</span>  . </h1></Typography>
                      </Stack>
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button className={'game-re'} variant="contained" color="success"
                            onClick={() => setResult(false)}></Button>
                  </DialogActions>
                </Dialog>
              </div>
              :
              null
          ))
      }
    {/*    ()*/}
      {
        props.chatt
          .filter(item => item.topic === 'apply')
          .map((item, idx) => (
            item.msg === 'regame' && item.name !== props.name ?
              <div key={idx}>
                <Dialog open={result2} onClose={() => setResult2(false)}>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                      <Stack spacing={5} direction={'row'} sx={{
                        justifyContent: 'center',
                        alignItems: 'center',
                        paddingY: 2,
                      }}>
                        <Typography variant="h3" color={'black'}><h1><b><span className={'text-primary'}>{item.name}</span>  . <br/><br/>?</b></h1></Typography>
                      </Stack>
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button className={'game-re'} variant="contained" color="success"
                            onClick={accept}></Button>
                    <Button className={'game-exit'} variant="contained" color="error"
                            onClick={reject}></Button>
                  </DialogActions>
                </Dialog>
              </div>
              :
              null
        ))
      }
      {/*   */}
      {
        props.chatt.
          filter(item => item.topic === 'answer')
          .map((item, idx) => (
            //   
            item.msg === 'yes' && item.name !== props.name && idx === 0 ?
              <div key={idx}>
                <Dialog open={result3} onClose={() => setResult3(false)}>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                      <Stack spacing={5} direction={'row'} sx={{
                        justifyContent: 'center',
                        alignItems: 'center',
                        paddingY: 2,
                      }}>
                        <Typography variant="h3" color={'black'}><h1><b><span className={'text-primary'}>{item.name}</span>  .</b></h1></Typography>
                      </Stack>
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button className={'game-re'} variant="contained" color="success"
                            onClick={() => {
                            navi(`/playground/${item.ip}`);
                            window.location.reload();
                            }}></Button>
                  </DialogActions>
                </Dialog>
              </div>
              //   
              : item.msg === 'no' && item.name !== props.name && idx === 0 ?
              <div key={idx}>
                <Dialog open={result3} onClose={() => setResult3(false)}>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                      <Stack spacing={5} direction={'row'} sx={{
                        justifyContent: 'center',
                        alignItems: 'center',
                        paddingY: 2,
                      }}>
                        <Typography variant="h3" color={'black'}><h1><b><span className={'text-primary'}>{item.name}</span>  .</b></h1></Typography>
                      </Stack>
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button className={'game-re'} variant="contained" color="success"
                            onClick={() => {
                              setResult3(false)}}></Button>
                  </DialogActions>
                </Dialog>
              </div>
              :
              null
        ))
      }
    </div>
  )
}

export default Tool;