import React, {useEffect, useState} from "react";
import {Box, Button, Grid, Typography} from "@mui/material";
import {Link, useNavigate} from "react-router-dom";
import axios from "axios";

function Banner(props) {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [ip, setIp] = useState('');
  
  // playground  ip 
  useEffect(() => {
    console.log("useEffect ")
    axios.get(`http://3.39.195.198:8080/server/getIp`) // ip  axios
      .then(res => {
        console.log(res.data);
        setIp(res.data);
      })
      .catch(err => {
        alert(``);
      });
    
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    
    window.addEventListener("resize", handleResize);
    
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);


//     windowWidth  
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    
    window.addEventListener("resize", handleResize);
    
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);
  
  const navigate = useNavigate();
  
  const goPlay = () => navigate(`/playground/${ip}`);
  
  return (
    <Box
      sx={
        windowWidth >= 960
          ? {
            position: "fixed",
            bottom: 0,
            left: 0,
            right: 0,
            textAlign: "center",
            border: '20px solid transparent',
            borderImage: 'url("/img/main/human-border.png") 50 round',
          }
          : {
            position: "fixed",
            bottom: 0,
            marginY: 10,
            marginX: 5,
            textAlign: "center",
            border: '20px solid transparent',
            borderImage: 'url("/img/main/human-border.png") 50 round',
          }
      }
    >   {/* Box mui div   , sx  inline-style    */}
      
      {/*<Box sx={{*/}
      
      {/*    // position: "fixed",*/}
      {/*    // bottom: 0,*/}
      {/*    // left: 0,*/}
      {/*    // right: 0,*/}
      {/*    display: 'flex',*/}
      {/*    alignItems: 'center',*/}
      {/*    justifyContent: 'center',*/}
      {/*    height: '100%',*/}
      {/*    marginBottom: '2em',*/}
      {/*    textAlign: "center",*/}
      {/*    border: '20px solid transparent',*/}
      {/*    borderImage: 'url("/img/main/human-border.png") 50 round',*/}
      {/*}}>*/}
      <Grid container spacing={0} sx={{
        bgcolor: 'black',
      }}>
        <Grid xs={0} md={4}>
          {windowWidth >= 960 && (
            <img src="/img/main/orc.jpg" alt="" style={{width: '100%', float: 'left', verticalAlign: 'middle'}} />
          )}
        </Grid>
        <Grid xs={12} md={4}>
          <Button sx={{width: '100%', height: '100%'}} onClick={goPlay}>
            <Box>
              <Typography variant="h4" sx={{marginY: '0.5em'}}>
                 
              </Typography>
              <h1> </h1>
            </Box>
          </Button>
        </Grid>
        <Grid xs={0} md={4}>
          {windowWidth >= 960 && (
            <img src="/img/main/human.jpg" alt="" style={{width: '100%', float: 'right', verticalAlign: 'middle'}}/>
          )}
        </Grid>
      </Grid>
    </Box>
  )
}

export default Banner;