import * as React from 'react';
import {Box, Link} from "@mui/material";
import Banner from "./Banner";
import {useEffect, useState} from "react";
import axios from "axios";
import {Outlet} from "react-router-dom";

function Main(props) {
    const [windowWidth, setWindowWidth] = useState(window.innerWidth);

//     windowWidth  
    useEffect(() => {
        // axios.get(`http://localhost:8080/server/getIp`) // ip  axios
        //     .then(res => {
        //         console.log(res.data);
        //         setIp(res.data);
        //     })
        //     .catch(err => {
        //         alert(` `);
        //     });

        const handleResize = () => {
            setWindowWidth(window.innerWidth);
        };

        window.addEventListener("resize", handleResize);

        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    return (
        <Box sx={{
            background: 'url("/img/main/omok.jpg") no-repeat center',
            height: '100vh',
            backgroundSize: 'cover',
        }}>
            {/*<Grid container spacing={0} sx={{placeItem: 'center'}}>*/}
            {/*    <Grid xs={3}>*/}

            {/*    </Grid>*/}
            {/*    <Grid xs={6} sx={{marginTop: '2em'}}>*/}
            {/*        <img src="/img/main/logo.png" alt="" style={{width: '90%'}}/>  /!*   /  ,  public *!/*/}
            {/*    </Grid>*/}
            {/*    <Grid xs={3}>*/}

            {/*    </Grid>*/}
            {/*</Grid>*/}
            <Box display="flex" alignItems="center" justifyContent="center" height={'100%'} marginBorder={'12em'}>  {/*    sx    */}
                {windowWidth >= 960
                    ?
                        <Link href={`#`} className={'d-flex align-items-center'}>
                            <img src="/img/main/logo.png" className={'mx-auto'} alt="" style={{width: '40%', marginBottom: '17em'}}/>
                        </Link>
                    :
                        <Link href={`#`}>
                            <img src="/img/main/logo.png" className={'mx-auto'}  alt="" style={{width: '80%', marginBottom: '25em'}}/>
                        </Link>
                }
            </Box>

            <Banner/>
        </Box>
    )
}

export default Main;